/* eslint-disable @typescript-eslint/no-unused-vars */
'use client';

import { useState, useEffect } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { Calendar, MapPin, Clock, Users, Star, Share, Heart, ArrowLeft, ShoppingCart } from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';

// Types
interface Event {
    eventId: number;
    title: string;
    description: string;
    shortDescription?: string;
    organizerId: number;
    organizerName: string;
    venueId: number;
    venueName: string;
    venueCity: string;
    categoryId: number;
    categoryName: string;
    startDateTime: string;
    endDateTime: string;
    imageUrl?: string;
    bannerImageUrl?: string;
    status: string;
    isPublished: boolean;
    isFeatured: boolean;
    createdAt: string;
    tags?: string;
    maxAttendees: number;
    basePrice: number;
    currency: string;
    isOnline: boolean;
    onlineUrl?: string;
    ticketsSold: number;
    availableTickets: number;
}

interface TicketType {
    ticketTypeId: number;
    eventId: number;
    eventTitle: string;
    name: string;
    description?: string;
    price: number;
    quantityAvailable: number;
    quantitySold: number;
    quantityRemaining: number;
    saleStartDate?: string;
    saleEndDate?: string;
    minQuantityPerOrder: number;
    maxQuantityPerOrder: number;
    isActive: boolean;
    isOnSale: boolean;
    sortOrder: number;
}

interface CartItem {
    ticketTypeId: number;
    name: string;
    price: number;
    quantity: number;
    maxQuantity: number;
}

export default function EventDetailsPage() {
    const params = useParams();
    const router = useRouter();
    const { isAuthenticated } = useAuth();
    const eventId = params.id as string;

    const [event, setEvent] = useState<Event | null>(null);
    const [ticketTypes, setTicketTypes] = useState<TicketType[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');
    const [cart, setCart] = useState<CartItem[]>([]);
    const [showTickets, setShowTickets] = useState(false);

    useEffect(() => {
        if (eventId) {
            fetchEventDetails();
            fetchTicketTypes();
        }
    }, [eventId]);

    const fetchEventDetails = async () => {
        try {
            const response = await fetch(`http://localhost:5251/api/events/${eventId}`);
            if (response.ok) {
                const data = await response.json();
                setEvent(data);
            } else {
                setError('Event not found');
            }
        } catch (error) {
            setError('Failed to load event details');
        } finally {
            setLoading(false);
        }
    };

    const fetchTicketTypes = async () => {
        try {
            const response = await fetch(`http://localhost:5251/api/tickets/event/${eventId}/ticket-types`);
            if (response.ok) {
                const data = await response.json();
                setTicketTypes(data);
            }
        } catch (error) {
            console.error('Failed to fetch ticket types:', error);
        }
    };

    const addToCart = (ticketType: TicketType, quantity: number) => {
        setCart(prevCart => {
            const existingItem = prevCart.find(item => item.ticketTypeId === ticketType.ticketTypeId);
            let newCart;

            if (existingItem) {
                newCart = prevCart.map(item =>
                    item.ticketTypeId === ticketType.ticketTypeId
                        ? { ...item, quantity: Math.min(item.quantity + quantity, item.maxQuantity) }
                        : item
                );
            } else {
                newCart = [...prevCart, {
                    ticketTypeId: ticketType.ticketTypeId,
                    name: ticketType.name,
                    price: ticketType.price,
                    quantity,
                    maxQuantity: ticketType.maxQuantityPerOrder
                }];
            }

            // Save to localStorage for checkout page
            localStorage.setItem('eventCart', JSON.stringify(newCart));

            return newCart;
        });
    };

    const updateCartQuantity = (ticketTypeId: number, quantity: number) => {
        if (quantity === 0) {
            setCart(cart.filter(item => item.ticketTypeId !== ticketTypeId));
        } else {
            setCart(cart.map(item =>
                item.ticketTypeId === ticketTypeId ? { ...item, quantity } : item
            ));
        }
    };

    const getTotalPrice = () => {
        return cart.reduce((total, item) => total + (item.price * item.quantity), 0);
    };

    const handlePurchase = () => {
        if (!isAuthenticated) {
            router.push('/login');
            return;
        }
        // Navigate to checkout (we'll build this next)
        router.push(`/checkout?eventId=${eventId}`);
    };

    const formatDate = (dateString: string) => {
        return new Date(dateString).toLocaleDateString('en-US', {
            weekday: 'long',
            year: 'numeric',
            month: 'long',
            day: 'numeric'
        });
    };

    const formatTime = (dateString: string) => {
        return new Date(dateString).toLocaleTimeString('en-US', {
            hour: 'numeric',
            minute: '2-digit'
        });
    };

    if (loading) {
        return (
            <div className="min-h-screen bg-gray-50 flex items-center justify-center">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
            </div>
        );
    }

    if (error || !event) {
        return (
            <div className="min-h-screen bg-gray-50 flex items-center justify-center">
                <div className="text-center">
                    <h1 className="text-2xl font-bold text-gray-900 mb-4">Event Not Found</h1>
                    <p className="text-gray-600 mb-8">{error}</p>
                    <button
                        onClick={() => router.push('/events')}
                        className="bg-blue-600 text-white px-6 py-2 rounded-lg hover:bg-blue-700"
                    >
                        Back to Events
                    </button>
                </div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gray-50">
            {/* Back Button */}
            <div className="bg-white border-b border-gray-200">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
                    <button
                        onClick={() => router.back()}
                        className="flex items-center text-gray-600 hover:text-gray-900"
                    >
                        <ArrowLeft className="h-5 w-5 mr-2" />
                        Back to Events
                    </button>
                </div>
            </div>

            {/* Hero Section */}
            <div className="relative">
                <div className="h-64 md:h-96 bg-gradient-to-r from-blue-600 to-purple-700">
                    {event.bannerImageUrl || event.imageUrl ? (
                        <img
                            src={event.bannerImageUrl || event.imageUrl}
                            alt={event.title}
                            className="w-full h-full object-cover"
                        />
                    ) : (
                        <div className="w-full h-full flex items-center justify-center">
                            <Calendar className="h-24 w-24 text-white opacity-50" />
                        </div>
                    )}
                </div>

                {/* Event Title Overlay */}
                <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/70 to-transparent">
                    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
                        <div className="text-white">
                            <h1 className="text-3xl md:text-5xl font-bold mb-4">{event.title}</h1>
                            <div className="flex flex-wrap items-center gap-4 text-lg">
                                <div className="flex items-center">
                                    <Calendar className="h-5 w-5 mr-2" />
                                    <span>{formatDate(event.startDateTime)}</span>
                                </div>
                                <div className="flex items-center">
                                    <Clock className="h-5 w-5 mr-2" />
                                    <span>{formatTime(event.startDateTime)}</span>
                                </div>
                                <div className="flex items-center">
                                    <MapPin className="h-5 w-5 mr-2" />
                                    <span>{event.isOnline ? 'Online Event' : `${event.venueName}, ${event.venueCity}`}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Main Content */}
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
                <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                    {/* Event Details */}
                    <div className="lg:col-span-2">
                        <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6 mb-6">
                            <h2 className="text-2xl font-bold text-gray-900 mb-4">About This Event</h2>
                            <div className="prose max-w-none">
                                <p className="text-gray-600 leading-relaxed">{event.description}</p>
                            </div>

                            {event.tags && (
                                <div className="mt-6">
                                    <h3 className="text-lg font-semibold text-gray-900 mb-2">Tags</h3>
                                    <div className="flex flex-wrap gap-2">
                                        {event.tags.split(',').map((tag, index) => (
                                            <span key={index} className="px-3 py-1 bg-gray-100 text-gray-700 rounded-full text-sm">
                                                {tag.trim()}
                                            </span>
                                        ))}
                                    </div>
                                </div>
                            )}
                        </div>

                        {/* Organizer Info */}
                        <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6">
                            <h3 className="text-lg font-semibold text-gray-900 mb-4">Event Organizer</h3>
                            <div className="flex items-center">
                                <div className="w-12 h-12 bg-blue-100 rounded-full flex items-center justify-center">
                                    <Users className="h-6 w-6 text-blue-600" />
                                </div>
                                <div className="ml-4">
                                    <p className="font-medium text-gray-900">{event.organizerName}</p>
                                    <p className="text-gray-600">Event Organizer</p>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* Ticket Purchase Sidebar */}
                    <div className="lg:col-span-1">
                        <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6 sticky top-4">
                            <h3 className="text-xl font-bold text-gray-900 mb-4">Get Tickets</h3>

                            {/* Event Stats */}
                            <div className="grid grid-cols-2 gap-4 mb-6">
                                <div className="text-center">
                                    <p className="text-2xl font-bold text-blue-600">{event.ticketsSold}</p>
                                    <p className="text-sm text-gray-600">Tickets Sold</p>
                                </div>
                                <div className="text-center">
                                    <p className="text-2xl font-bold text-green-600">{event.availableTickets}</p>
                                    <p className="text-sm text-gray-600">Available</p>
                                </div>
                            </div>

                            {/* Ticket Types */}
                            <div className="space-y-4">
                                {ticketTypes.length === 0 ? (
                                    <p className="text-gray-600 text-center py-4">No tickets available yet</p>
                                ) : (
                                    ticketTypes.map((ticketType) => (
                                        <div key={ticketType.ticketTypeId} className="border border-gray-200 rounded-lg p-4">
                                            <div className="flex justify-between items-start mb-2">
                                                <div>
                                                    <h4 className="font-semibold text-gray-900">{ticketType.name}</h4>
                                                    {ticketType.description && (
                                                        <p className="text-sm text-gray-600">{ticketType.description}</p>
                                                    )}
                                                </div>
                                                <p className="text-lg font-bold text-gray-900">
                                                    ${ticketType.price.toFixed(2)}
                                                </p>
                                            </div>

                                            <div className="flex justify-between items-center text-sm text-gray-600 mb-3">
                                                <span>{ticketType.quantityRemaining} remaining</span>
                                                <span>Max {ticketType.maxQuantityPerOrder} per order</span>
                                            </div>

                                            {ticketType.isOnSale && ticketType.quantityRemaining > 0 ? (
                                                <div className="flex items-center gap-2">
                                                    <select
                                                        className="flex-1 border border-gray-300 rounded px-3 py-2"
                                                        onChange={(e) => {
                                                            const quantity = parseInt(e.target.value);
                                                            if (quantity > 0) {
                                                                addToCart(ticketType, quantity);
                                                            }
                                                        }}
                                                    >
                                                        <option value="0">Select quantity</option>
                                                        {Array.from({ length: Math.min(ticketType.maxQuantityPerOrder, ticketType.quantityRemaining) }, (_, i) => (
                                                            <option key={i + 1} value={i + 1}>{i + 1}</option>
                                                        ))}
                                                    </select>
                                                </div>
                                            ) : (
                                                <button
                                                    disabled
                                                    className="w-full bg-gray-300 text-gray-500 py-2 rounded-lg cursor-not-allowed"
                                                >
                                                    {ticketType.quantityRemaining === 0 ? 'Sold Out' : 'Not Available'}
                                                </button>
                                            )}
                                        </div>
                                    ))
                                )}
                            </div>

                            {/* Cart Summary */}
                            {cart.length > 0 && (
                                <div className="mt-6 border-t border-gray-200 pt-6">
                                    <h4 className="font-semibold text-gray-900 mb-4">Your Selection</h4>
                                    <div className="space-y-2 mb-4">
                                        {cart.map((item) => (
                                            <div key={item.ticketTypeId} className="flex justify-between items-center">
                                                <div>
                                                    <span className="text-sm text-gray-600">{item.name}</span>
                                                    <div className="flex items-center gap-2 mt-1">
                                                        <button
                                                            onClick={() => updateCartQuantity(item.ticketTypeId, item.quantity - 1)}
                                                            className="w-6 h-6 border border-gray-300 rounded flex items-center justify-center text-gray-600 hover:bg-gray-50"
                                                        >
                                                            -
                                                        </button>
                                                        <span className="text-sm">{item.quantity}</span>
                                                        <button
                                                            onClick={() => updateCartQuantity(item.ticketTypeId, item.quantity + 1)}
                                                            className="w-6 h-6 border border-gray-300 rounded flex items-center justify-center text-gray-600 hover:bg-gray-50"
                                                            disabled={item.quantity >= item.maxQuantity}
                                                        >
                                                            +
                                                        </button>
                                                    </div>
                                                </div>
                                                <span className="font-semibold">${(item.price * item.quantity).toFixed(2)}</span>
                                            </div>
                                        ))}
                                    </div>

                                    <div className="border-t border-gray-200 pt-4">
                                        <div className="flex justify-between items-center mb-4">
                                            <span className="text-lg font-semibold">Total:</span>
                                            <span className="text-xl font-bold text-blue-600">${getTotalPrice().toFixed(2)}</span>
                                        </div>

                                        <button
                                            onClick={handlePurchase}
                                            className="w-full bg-blue-600 hover:bg-blue-700 text-white font-semibold py-3 px-4 rounded-lg transition-colors duration-200 flex items-center justify-center"
                                        >
                                            <ShoppingCart className="h-5 w-5 mr-2" />
                                            Purchase Tickets
                                        </button>
                                    </div>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}